<template>
    <div>
      <l-map
        :zoom="zoom"
        :center="center"
        :min-zoom="minZoom"
        :max-zoom="maxZoom"
        :attributionControl="attributionControl"
        :zoomend="zoomend"
      >
        <l-tilelayer
          :url="url"
          :attribution="attribution"
        ></l-tilelayer>
        <l-marker
          :position="center"
          :title="title"
          :opacity="opacity"
          :draggable="draggable"
        >
          <l-tooltip content="a tooltip"></l-tooltip>
        </l-marker>
      </l-map>
    </div>
</template>

<script>
// import
export default {
  name: 'Leaflet',
  data () {
    return {
      // 定义的每个data 属性必须有注释
      // 注释规则： //
      // 默认地图放大级别
      zoom: 13,
      // 默认地图的中心经纬度
      center: [39.914271, 116.404269],
      // 地图标记的位置
      marker: [39.914271, 116.404269],
      // 地图最小放大级别
      minZoom: 5,
      // 地图最大放大级别
      maxZoom: 18,
      // 地图样式图层获取路径
      // url: 'http://{s}.tile.osm.org/{z}/{x}/{y}.png',
      url: 'http://map.geoq.cn/ArcGIS/rest/services/ChinaOnlineStreetPurplishBlue/MapServer/tile/{z}/{y}/{x}',
      // 地图样式图层的归属
      attribution: 'vue-leaflet_old',
      // 地图标记的提示
      title: 'vue-leaflet_old',
      // 标记的透明度
      opacity: 1,
      // 是否可被鼠标拖拽
      draggable: true,
      // 默认情况下是否将归属控件添加到地图
      attributionControl: false
    }
  },
  props: {
    // id: {
    //   default: '',
    //   type: String
    // }
  },
  components: {},
  // beforeRouteEnter () {
  // },
  // beforeRouteUpdate () {
  // },
  // beforeRouteLeave () {
  // },
  beforeCreate () {
  },
  created () {
  },
  beforeMount () {
  },
  mounted () {
  },
  beforeUpdate () {
  },
  updated () {
  },
  beforeDestroy () {
  },
  destroyed () {
  },
  watch: {},
  computed: {},
  directives: {},
  filter: {},
  methods: {
    // 方法调用不需要注释，方法定义需要注释
    // 方法注释规则 /** */
    /**
     * @desc 地图的当大级别变化之后触发的事件
     * @param {Number} zoom 放大级别
     */
    zoomend (zoom) {
      console.log('zoom:', zoom)
    }
  }
}
</script>

<style scoped>

</style>



